@font-face {
	font-family: "Whyte";
	src: url(/assets/ABCWhyteSemi-Mono-Regular.woff) format("woff");
	src: url(/assets/ABCWhyteSemi-Mono-Regular.woff2) format("woff2");
}
@font-face {
	font-family: "Whyte Inktrap Light";

	src: url(/assets/ABCWhyteInktrapSemi-Mono-Regular.woff) format("woff");
	src: url(/assets/ABCWhyteInktrapSemi-Mono-Regular.woff2) format("woff2");
}
@font-face {
	font-family: "Whyte Inktrap";
	font-weight: 200;
	src: url(/assets/ABCWhyteInktrap-Light.woff) format("woff");
	src: url(/assets/ABCWhyteInktrap-Light.woff2) format("woff2");
}
@font-face {
	font-family: "Galapagos";
	src: url(/assets/ABCGalapagosA-Bold-Trial.woff) format("woff");
	src: url(/assets/ABCGalapagosA-Bold-Trial.woff2) format("woff2");
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	max-height: 100vh;
	max-width: 100vw;
	padding-block: 20px;
	background: rgb(183 183 183);
	color: red;
	font-family: "Whyte", sans-serif;
}

button {
	cursor: pointer;
	font-family: "Galapagos";
	font-size: 2rem;
	border: none;
	background: none;
	outline: none;
	color: inherit;
}

.container {
	padding-block: 30px;

	display: flex;
	flex-direction: column;
	align-items: center;
}

/* Weather container */
.weather {
	position: relative;

	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 15px;
}

.weather__image {
	width: 300px;
}

.weather__location,
.weather__temperature {
	font-family: "Galapagos";
	font-size: 3rem;
	text-transform: uppercase;
}

.weather__description {
	font-family: "Whyte";
	text-transform: lowercase;
}

.weather__details {
	position: absolute;
	top: 58px;
	right: -200px;
	text-align: end;
}

.weather__details-info * {
	font-size: 0.8rem;
}

/* Search form */
.search__wrapper {
	margin-top: 40px;
	position: relative;
}

.search__button {
	position: absolute;
	margin-left: 15px;
}

.search__input {
	width: 300px;
	border: 1px solid red;
	background-color: #c5c5ff;
	padding: 0.6rem 0.2rem;
}

.search__msg {
	font-family: "Whyte";
	margin-top: 4rem;
	display: inline-block;
	width: 12rem;
}

